import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import noti from '../../assets/images/notification-bell.png'
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import { iniSocket } from 'src/socket'
const Navbar = () => {
    const socketRef = useRef(null)
    const navigate = useNavigate();
    const [showHover, setShowHover] = useState(false);
    const [inputValue, setInputValue] = useState("");
    const [notifications, setNotifications] = useState([]);


    const toastOptions = {
        position: "bottom-right",
        autoClose: 8000,
        pauseOnHover: true,
        draggable: true,
        theme: "dark",
    };
    useEffect(() => {
        const init = async () => {
            socketRef.current = await iniSocket()
            socketRef.current.emit('join', 'wallet_alert')
            socketRef.current.on('message', (data) => {
                toast(data, toastOptions);
            })
        }
        init()
    }, [])

    const handleInputChange = (event) => {
        setInputValue(event.target.value);
    };

    const handleKeyDown = (event) => {
        if (event.key === "Enter") {
            submitInputValue();
        }
    };

    const submitInputValue = () => {
        if (inputValue.length === 42) {
            if (inputValue.startsWith("0x")) {
                console.log("Submitting input value:", inputValue);
                navigate(`/home/${inputValue}`)
            } else {
                toast.error("Wallet address must starts with '0x'", toastOptions)
            }
        }
        else {
            toast.error("Enter valid wallet address", toastOptions)
        }
    };

    const toggleHover = () => {
        setShowHover(!showHover);
    };
    const handleNotification = (notification) => {
        setNotifications((prevNotifications) => [...prevNotifications, notification]);
    };
    return (
        <>
            <StyledNavbar className="navbar navbar-expand-lg navbar-light bg-light">
                <h2 className="navbar-brand" href="#">
                    Blockchain Explorer
                </h2>

                <div className="collapse navbar-collapse" id="navbarNavDropdown">
                    <div className="navbar-nav">
                        <StyledInput type="text" placeholder="Search Any Address 0x.." onChange={handleInputChange}
                            onKeyDown={handleKeyDown} />
                    </div>
                </div>
                <NotificationIcon src={noti} onClick={toggleHover} />
                {showHover && (
                    <HoverDiv>
                        <NotificationList>
                            {notifications.map((notification, index) => (
                                <Notification key={index}>{notification.message}</Notification>
                            ))}
                        </NotificationList>
                        Notification Content
                    </HoverDiv>
                )}
            </StyledNavbar>
            <ToastContainer />
        </>
    );
};

const StyledNavbar = styled.nav`
    /* background: rgb(99,34,97);
    background: radial-gradient(circle, rgba(99,34,97,1) 0%, rgba(8,10,24,1) 58%); */
    background-color: transparent !important;
    display: flex;
    justify-content: space-between; /* Align items to the end */
    align-items: center;
    padding: 15px;    
    .navbar-brand {
        color: #3B92E2 !important;
        /* margin-right: 15rem !important; */
    }
    .navbar-nav .nav-link {
        justify-content: center !important ;
        align-items: center !important ;
        color: #3B92E2 !important;
    }
    .navbar-nav .nav-link:hover {
        color: lightblue !important;
    }
    .collapse {
        display: flex!important;
        justify-content: center !important; /* Center children horizontally */
        align-items: Center!important;
    }
`;

const StyledInput = styled.input`
    width: 500px; /* Set width of the input */
    height: 40px; /* Set height of the input */
    margin-right: 10rem; /* Reset margin */
    padding: 5px; /* Add padding for better appearance */
    border-radius: 12px; /* Make corners rounded */
    border: none; /* Remove default border */
    background  :#131313 ;
    color: lightblue ;
    &:focus{
        outline: none;
    }
`;
const NotificationIcon = styled.img`
    width: 30px; /* Set width of the notification icon */
    height: 30px; /* Set height of the notification icon */
    cursor: pointer;
    /* margin-right: 2rem; */
`;
const HoverDiv = styled.div`
    position: absolute;
    top: 60px; /* Adjust this value as needed */
    right: 0;
    width: 200px;
    background-color: #1B1B1B;
    border: 1px solid #ccc;
    border-radius: 5rem;
    padding: 10px;
`;
const NotificationList = styled.ul`
    list-style: none;
    padding: 0;
`;
const Notification = styled.li`
    background-color: rgba(255, 255, 255, 0.1);
    padding: 0.5rem 1rem;
    margin-bottom: 0.5rem;
    border-radius: 5px;
`;

export default Navbar;
