import React, { Component, Suspense } from 'react'
import { HashRouter, Navigate, Route, Routes, useLocation } from 'react-router-dom'
import './scss/style.scss'
import { ToastContainer } from 'react-toastify'
import "react-toastify/dist/ReactToastify.css";
import { useSelector } from 'react-redux';
import Login from './views/pages/login/Login';
import Navbar from './views/pages/navbar';
import Home from './views/pages/home';
import Search from './views/pages/search';


const loading = (
    <div className="pt-3 text-center">
        <div className="sk-spinner sk-spinner-pulse"></div>
    </div>
)

class App extends Component {
    render() {
        return (
            <>
                <div>
                    <ToastContainer></ToastContainer>
                </div>
                <HashRouter>
                    <Suspense fallback={loading}>
                        <Routes>
                            <Route exact path="/" name="Login Page" element={<Login />} />
                            <Route exact path='/home' name="home page" element={<Home/>}/>
                            <Route path='/home/:address' name="Search Page" element={<Search/>}/>
                            
                            {/* <Route exact path="/Dashboard" name="Login Page" element={<Dashboard />} /> */}
                            {/* <Route exact path="/404" name="Page 404" element={<Page404 />} />
                            <Route exact path="/500" name="Page 500" element={<Page500 />} /> */}
                            {/* <Route path="*" name="Home" element={<ProtectedRoute><DefaultLayout /> </ProtectedRoute>} /> */}
                        </Routes>
                    </Suspense>
                </HashRouter>
            </>
        )
    }
}
const ProtectedRoute = ({ children }) => {
    const { user } = useSelector((state) => state.auth)
    const location = useLocation()
    return !user ? <Navigate to="/login" state={{ from: location }} /> : children
}
export default App
