import axios from "axios";
import {
    checkToken,
    loginRoute,
    walletAlertRoute,
    walletLogs,
} from "../utils/APIRoutes";

export const login = (data) => {
    return new Promise((resolve, reject) => {
        let config = {
            method: "post",
            url: loginRoute,
            headers: {
                "Content-Type": "application/json",
            },
            withCredentials: true,
            data,
        };
        axios(config)
            .then((response) => {
                resolve(response);
            })
            .catch((err) => {
                reject(err);
            });
    });
};

export const logout = () => {
    return new Promise((resolve, reject) => {
        let config = {
            method: "post",
            url: process.env.REACT_APP_API_URL + "/api/user/logout",
            headers: {
                "Content-Type": "application/json",
            },
            withCredentials: true,
        };
        axios(config)
            .then((response) => {
                resolve(response);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
