import React, { useEffect, useState } from "react";
import Navbar from "./navbar"; // Assuming Navbar component is in the same directory as Home.js
import styled from "styled-components";

const Home = () => {
    const [text, setText] = useState("");
    const sentences = ["Welcome to the Blockchain Explorer", "Search any Eth wallet address"];

    useEffect(() => {
        let charIndex = 0;
        let sentenceIndex = 0;

        const typeSentence = () => {
            if (charIndex < sentences[sentenceIndex].length) {
                setText(sentences[sentenceIndex].substring(0, charIndex + 1));
                charIndex++;
                setTimeout(typeSentence, 100); // Adjust typing speed here
            } else {
                setTimeout(() => {
                    eraseSentence();
                }, 2000); // Delay before erasing text
            }
        };

        const eraseSentence = () => {
            if (charIndex >= 0) {
                setText(sentences[sentenceIndex].substring(0, charIndex));
                charIndex--;
                setTimeout(eraseSentence, 50); // Adjust erasing speed here
            } else {
                sentenceIndex = (sentenceIndex + 1) % sentences.length;
                setTimeout(() => {
                    typeSentence();
                }, 1000); // Delay before typing next sentence
            }
        };

        typeSentence();

        return () => {
            // Clean up any pending timeouts
            clearTimeout();
        };
    }, []);
    return (

        <div>
            <StyledHome>
                <Navbar />
                <div className="body">
                    <h1>{text}</h1>
                </div>
            </StyledHome >
        </div>



    );
};
const StyledHome = styled.div`
   
    background: rgb(99,34,97);
    background: radial-gradient(circle, rgba(99,34,97,1) 0%, rgba(8,10,24,1) 58%);  
    height: 100vh;
   .body{
    padding: 3rem;
        /* display: flex; */
        flex-direction: column;
        justify-content: flex-start; 
        align-items: center;
        color: #fff; 
    }
    h1 {
        font-size: 3rem;
    }
`;

export default Home;