import React from "react";
import styled from "styled-components";

const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
`;

const TableHeader = styled.th`
  background-color: #3B92E2;
  color: #131313;
  padding: 8px;
  text-align: left;
`;

const TableData = styled.td`
  padding: 8px;
  border-bottom: 1px solid #3B92E2;
`;

const TransactionTable = ({ transactions }) => {
  return (
    <Table>
      <thead>
        <tr>
          <TableHeader>Hash</TableHeader>
          <TableHeader>Block Number</TableHeader>
          <TableHeader>To</TableHeader>
          <TableHeader>Value (ETH)</TableHeader>
        </tr>
      </thead>
      <tbody>
        {transactions.map((transaction) => (
          <tr key={transaction.hash}>
            <TableData>{transaction.hash}</TableData>
            <TableData>{transaction.blockNumber}</TableData>
            <TableData>{transaction.to}</TableData>
            <TableData>{transaction.value}</TableData>
          </tr>
        ))}
      </tbody>
    </Table>
  );
};

export default TransactionTable;
