import React, { useEffect, useState } from 'react'
import Navbar from './navbar' // Assuming Navbar component is in the same directory as Home.js
import styled from 'styled-components'
import { SetWalletAlert, getLastTenTransactions, getTransactions } from 'src/http'
import { useParams } from 'react-router-dom'
import TransactionTable from './TransactionTable.jsx' // Import the TransactionTable component
import { ToastContainer, toast } from 'react-toastify'

const Search = () => {
    const [previousTnxData, setPreviousTnxData] = useState({})
    const { address } = useParams()
    const [dataLoading, setDataLoading] = useState(true)
    const toastOptions = {
        position: 'bottom-right',
        autoClose: 8000,
        pauseOnHover: true,
        draggable: true,
        theme: 'dark',
    }

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await getLastTenTransactions(address)
                setPreviousTnxData(response.data)
                setDataLoading(false)
            } catch (error) {
                console.error('Error fetching transactions:', error)
            }

        }

        fetchData()
    }, [address])
    if (dataLoading) {
        return <div>loading</div>
    } else {
        const getOneYearData = async () => {
            setDataLoading(true)
            try {
                const response = await getTransactions(address, 1)
                setPreviousTnxData(response.data)
                setDataLoading(false)
            } catch (error) {
                console.error('Error fetching transactions:', error)
            }
        }
        const WalletAlert = async () => {
            try {
                console.log('dfgh')
                const email = localStorage.getItem('email')
                console.log(email);
                const response = await SetWalletAlert(address, email)
                console.log(response)

                if (response) {
                    toast('Alert Successful', toastOptions)
                } else {
                    toast.error('Alert Failed or Inactive', toastOptions)
                }
            } catch (error) {
                console.error('Error setting wallet alert:', error)
                toast.error('Something went wrong', toastOptions)
            }
        }
        return (
            <>
                <div>
                    <StyledHome>
                        <Navbar />
                        <div className="body">
                            <h3>Transaction History of address {address} </h3>
                            <button onClick={() => WalletAlert()}> Set Alert</button>
                            <TransactionTable transactions={previousTnxData} />
                            <button onClick={() => getOneYearData()}>Show More</button>
                        </div>
                        <div></div>
                    </StyledHome>
                </div>
                <ToastContainer />
            </>
        )
    }
}

const StyledHome = styled.div`
  background: rgb(99, 34, 97);
  background: radial-gradient(circle, rgba(99, 34, 97, 1) 0%, rgba(8, 10, 24, 1) 58%);
  /* height: 100; */
  .body {
    padding: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    color: #fff;
  }
  h3 {
    padding: 2rem;
  }
`

export default Search
